<template>
    <div id="modal" v-show="isOn">
      <div class="modal-container">
        <div class="modal-box">
          <button class="close-btn" @click.prevent="closeLoginBtn">X</button>
          <div class="modal-title"><img :src="logoImg" alt="로고이미지"></div>
          <form action="">
            <input v-model="이메일" type="text" id="userId" name="userId" maxlength="16" placeholder="이메일">
            <input v-model="비밀번호" type="password" id="userPw" name="userPw" maxlength="16" placeholder="비밀번호" autocomplete="false">
          </form>
          <button class="log-in">로그인</button>
          <div class="help-box">
            <span>아이디(이메일)찾기</span>
            <li></li>
            <span>비밀번호 찾기</span>
            <li></li>
            <span>회원가입</span>
          </div>
          <div class="login-icon">
            <span>간편로그인</span>
            <div class="logicon">
              <a href="#" v-for="item,idx in logiconImg" :key="idx"><img :src="item.이미지" :alt="item.이름"></a>
            </div>
          </div>
        </div>
      </div>
    </div>
</template>

<script>
import axios from 'axios';

export default {
    name : 'ModalComponent',
    props:[],
    data(){
      return{
        이메일 : '',
        비밀번호 :'',
        logiconImg:[],
        logoImg : '',
        // isOn : false,
      }
    },
    computed:{
      isOn(){
        return this.$store.getters.getIsOn 
      }
    },
    methods:{
      closeLoginBtn(){
        this.$store.dispatch('acModalClose', false)
      }
    },
    created(){
      axios({
        url:'./data/modal.json',
        method: "GET"
      }).
      then((res)=>{
        console.log(res.data);
        console.log("연결성공");
        this.logiconImg = res.data.logiconImg;
        this.logoImg = res.data.logoImg;

      })
      .catch((err)=>{
        console.log('실패');
        console.log(err);
      })
    }


}
</script>

<style lang="scss" scoped>

  #wrap #modal {
    position: fixed;
    z-index: 10;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;  
    background-color: rgb(0, 0, 0,0.5);
    .modal-container{
        margin-left: 40%;
        margin-top: 10%;
        width: 360px;
        height: 432px;
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: #fff;
        border: 1px solid transparent;
        border-radius: 8px;
        .modal-box{
            width: 312px;
            position: relative;
            .close-btn{
              position: absolute;
              right: 0;
              top: -15px;
              width: 20px;
              height: 20px;
              background-color: #fff;
              border: transparent;
              font-size: 18px;
              color: #303030;
            }
            .modal-title{
              width: 100%;
              text-align: center;
              img{
                width: 135px;
                height : 24px;
              }
            }
            form{
              width: 100%;
              height: 140px;
              display: flex;
              flex-flow: column;
              justify-content: space-between;
              padding: 16px 0;
              input{
                width: 100%;
                height: 48px;
                padding: 13px 12px;
                background-color: #fff;
                border: 1px solid #ddd;
                border-radius: 5px;
                outline-offset: -1px;
              }
              &::placeholder {
                color: #d46868;
                font-weight: 600;
              }
              &:focus{
                outline: 1px solid #07c550;
              }
            }
            button{
              width: 100%;
              height: 48px;
              background-color: #00c471;
              border: 1px solid #00c471;
              border-radius: 3px;
              cursor: pointer;
            }
            .help-box{
              width: 100%;
              display: flex;
              justify-content: center;
              align-items: center;
              color: #616568;
              padding: 10px 0 10px 20px;
              span{
                font-size: 12px;
                text-decoration: underline;
                cursor: pointer;
              }
              li::before{
                display: inline-block;
                content: '';
                width: 1px;
                height: 10px;
                margin-top: -11px;
                margin: 0 8px;
              }
            }
            .login-icon{
              width: 100%;
              display: flex;
              flex-flow: column;
              span{
                width: 100%;
                font-size: 11px;
                text-align: center;
                padding-top: 10px;
                padding-bottom: 10px;
                color: #abb0b5;
                border-bottom: 1px solid #eee;
              }
              .logicon{
                width: 100%;
                display: flex;
                justify-content: center;
                padding-top: 20px;
                a{
                  width: 44px;
                  height: 44px;
                  margin: 0 5px;
                  img{
                      width: 44px;
                      height: 44px;
                      border: 1px solid transparent;
                      border-radius: 5px;
                    }
                  }
              }
            }

        }
    }
  }

</style>