<template>
  <!-- 백그라운드 이미지 -->
      <section class="section section8">
        <div class="s8container">
          <div class="s8-slide-box">
            <!-- 슬라이드1 -->
            <div :class="['s8-slide', 's8slide'+(idx+1)]" v-for="item,idx in s8Slide" :key="idx">
              <div class="s8textBox">
                <h1>
                  <span>{{item.title}}</span>
                </h1>
                <a href="#">{{item.linkBtn}}</a>
              </div>
            </div>
          </div>
          <!-- 슬라이드8 버튼  -->
          <div class="s8-button-box">
            <span class="s8-left-btn">
              <li></li>
            </span>
            <span class="s8-right-btn">
              <li></li>
            </span>
          </div>
        </div>
      </section>
</template>

<script>
import $ from 'jquery';
import axios from 'axios';


export default {
    name:'Section8Component',
    props:[],
    data(){
      return{
        s8Slide:[]
      }
    },
    created(){
      axios({
        url : './data/sec8text.json'
      }).then((res)=>{
        this.s8Slide = res.data.s8Slide
      }).catch((err)=>{
        console.log(err)
      })
    },
    mounted(){
      
      let s8cnt = 0;

      function s8slide(){
        $('.s8-slide-box').stop().animate({marginLeft: `${-100 * s8cnt}%` },600)

        if (s8cnt>1){
          $('.s8-right-btn').animate({opacity: '0.2'});
        }
        if (s8cnt < 1){
          $('.s8-left-btn').animate({opacity: '0.2'});
        }
      }

      $('.s8-right-btn').on({
        click(){
          if (s8cnt>1){
            s8cnt = 1;
          }          
          s8nextCount();
          $('.s8-left-btn').animate({opacity: '1'});
        }
      })
      $('.s8-left-btn').on({
        click(){
          if(s8cnt>1){
            s8cnt = 2;
          }
          if (s8cnt < 1){
            s8cnt = 1;
          }
          s8prevCount();
          $('.s8-right-btn').animate({opacity: '1'});
        }
      })



      //다음 카운트
      function s8nextCount() {
        s8cnt++;
        s8slide();
      }

      //이전 카운트
      function s8prevCount() {
        s8cnt --;    
        s8slide();    
      }

    }
}
</script>

<style scoped>

  /* 섹션8 */

  #wrap #main .section8 {
    width: 100%;
    height: 230px;
    display: flex;
    align-items: center;
  }

  #wrap #main .section8 .s8container {
    width: 100%;
    height: 182px;
    position: relative;
  }

  /* 이미지 세개, 왼쪽 오른쪽으로 넘기기 */
  #wrap #main .section8 .s8container .s8-slide-box {
    /* 이미지3개 300% */
    width: 300%;
    height: 100%;
    background: url() no-repeat 50% 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    /* margin-left: -100%; */
  }

  #wrap #main .section8 .s8container .s8-slide-box .s8-slide {
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  #wrap #main .section8 .s8container .s8-slide-box .s8-slide .s8textBox {
    width: 1200px;
  }

  #wrap #main .section8 .s8container .s8-slide-box .s8-slide .s8textBox>h1 {
    width: 1136px;
    height: 70px;
  }

  #wrap #main .section8 .s8container .s8-slide-box .s8-slide .s8textBox>h1>span {
    display: block;
    width: 300px;
    font-size: 16px;
    color: #404040;
  }

  #wrap #main .section8 .s8container .s8-slide-box .s8slide3 .s8textBox>h1>span {
    display: block;
    width: 300px;
    font-size: 16px;
    color: #ffffff;
  }

  #wrap #main .section8 .s8container .s8-slide-box .s8-slide .s8textBox>a {
    height: 40px;
    font-size: 15px;
    font-weight: 600;
    border: 1px solid transparent;
    padding: 9px 10px;
    border-radius: 5px;
    background-color: #fff;
    cursor: pointer;
    color: #1dc078;
  }


  #wrap #main .section8 .s8container .s8-slide-box .s8slide1 {
    background-image: url(../../../../public/images/s8slide1.jpg);
  }

  #wrap #main .section8 .s8container .s8-slide-box .s8slide2 {
    background-image: url(../../../../public/images/s8slide2.jpg);
  }

  #wrap #main .section8 .s8container .s8-slide-box .s8slide3 {
    background-image: url(../../../../public/images/s8slide3.jpg);
  }


  /* 섹션8버튼 */
  #wrap #main .section8 .s8container .s8-button-box {
    width: 70px;
    height: 35px;
    position: absolute;
    top: 75%;
    right: 16.5%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 3px;

  }

  #wrap #main .section8 .s8container .s8-button-box span {
    display: block;
    width: 28px;
    height: 28px;
    background-color: #fff;
    border: 1px solid transparent;
    border-radius: 3px;
    cursor: pointer;
    box-shadow: 0px 6px 10px -6px #666;
  }

  /* 왼쪽버튼모양 */
  #wrap #main .section8 .s8container .s8-button-box span.s8-left-btn li {
    display: inline-block;
    content: '';
    width: 10px;
    height: 10px;
    border-left: 1px solid #1e1e1e;
    border-bottom: 1px solid #1e1e1e;
    transform: rotate(45deg);
    margin-top: 0px;
    margin-left: 10px;
  }

  /* 오른쪽버튼모양 */
  #wrap #main .section8 .s8container .s8-button-box span.s8-right-btn li {
    display: inline-block;
    content: '';
    width: 10px;
    height: 10px;
    border-right: 1px solid #1e1e1e;
    border-top: 1px solid #1e1e1e;
    transform: rotate(45deg);
    margin-top: 0px;
    margin-left: 5px;
  }


</style>