<template>
      <section class="section section2">
        <div class="top">
          <div class="title">
            <h2>성장기회의 평등을 추구합니다</h2>
          </div>
          <div class="s2-search">
            <input type="text" placeholder="배우고 싶은 지식을 입력해보세요.">
            <button :class="['search-btn',{'on':isOn}]" @click.prevent="searchClick"><img :src="searchImg" alt=""></button>
          </div>
        </div>
        <!-- 섹션2하단 14개 아이콘슬라이드,버튼 -->
        <div class="bottom">
          <div class="s2-container">
            <!-- 아이콘슬라이드 -->
            <div class="s2-icon-box">
              <ul class="s2-icon-wrap">
                <li class="s2-icon" v-for="item,idx in iconImg" :key="idx">
                  <a class="s2-icon-content" href="#">
                    <img :src="item.img" alt="">
                    <p>{{item.text}}</p>
                  </a>
                </li>
              </ul>
            </div>
            <!-- 버튼화살표 -->
            <div class="s2-btn-arrow">
              <!-- 누르면 displaynone -->
              <div class="right-btn-container s2rightBtnC">
                <div class="right-btn-box">
                  <!-- 누르면 슬라이드 이동 -->
                  <div class="s2right-btn">
                    <li></li>
                  </div>
                </div>
              </div>
              <!-- 누르면 displaynone -->
              <div class="left-btn-container s2leftBtnC">
                <div class="left-btn-box">
                  <!-- 누르면 슬라이드 이동 -->
                  <div class="s2left-btn">
                    <li></li>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
</template>

<script>
import $ from 'jquery';
import axios from 'axios';

export default {
    name:'Section2Component',
    props:[],
    data(){
      return{
        searchImg:'./images/search.png',
        iconImg:[],
        isOn : false
      }
    },
    methods:{
      searchClick(){
        this.isOn = true;
      }
    },
    created(){
      axios({
        url : './data/sec2text.json',
        method : 'GET'
      }).then((res)=>{
        this.iconImg = res.data.iconImg
      }).catch((err)=>{
        console.log(err)
      })
    },

    mounted() {
      //돋보기 클릭 시
      $('.search-btn').on({
        click(){
          $('.search-btn').addClass('on');
        }
      });

      //s2 아이콘 슬라이드 버튼
      $('.s2right-btn').on({
        click(){
          // 104.4px
          $('.s2-icon-wrap').stop().animate({left : -365},300);
          $('.s2rightBtnC').hide();
          $('.s2leftBtnC').show();
        }
      });
      $('.s2left-btn').on({
        click(){
          // 104.4px
          $('.s2-icon-wrap').stop().animate({left : 0},300);
          $('.s2rightBtnC').show();
          $('.s2leftBtnC').hide();
        }
      });
     },
}
</script>

