<template>
      <section class="section section3">
        <div class="top">
          <div class="title">
            <h1>{{s3Title}}
              <span class="right-angle-box">
                <li class="right-angle"></li>
              </span>
            </h1>
            <p>무료 강의부터 가볍게 시작해 보세요.</p>
          </div>
        </div>
        <div class="bottom">
          <div class="s3-container">
            <div class="s3-slide-box">
              <!-- 이미지 10개 -->
              <ul class="s3-slid-wrap" >
                <li :class="['s3-slide', 's3slide'+(idx+1)]" v-for="item,idx in slidelist" :key="idx" >
                  <a class="s3-slide-content" href="#">
                    <img :src="item.img" alt="">
                    <div class="course-title">{{item.title}}</div>
                    <div class="instructor">{{item.instructor}}</div>
                    <div class="rating">{{item.rating}}</div>
                    <div class="price">{{item.price}}</div>
                    <div class="tag">
                      <span>{{item.tag}}</span>
                    </div>
                  </a>
                  <!-- 포지션으로 조절 -->
                  <a class='s3-slide-content-hover' href="#">
                    <div class="course-title" >{{item.title}}</div>
                    <div class="course-text" >
                      <span class="instructor" v-for="item2,i in item.hovtext" :key="i">{{item2}}</span>
                    </div>
                  </a>                  
                </li>
              </ul>
            </div>
            <!-- 버튼화살표 -->
            <div class="s3-btn-arrow">
              <!-- 누르면 displaynone -->
              <div class="right-btn-container">
                <div class="right-btn-box">
                  <!-- 누르면 슬라이드 이동 -->
                  <div class="s3right-btn">
                    <li></li>
                  </div>
                </div>
              </div>
              <!-- 누르면 displaynone -->
              <div class="left-btn-container">
                <div class="left-btn-box">
                  <!-- 누르면 슬라이드 이동 -->
                  <div class="s3left-btn">
                    <li></li>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
</template>

<script>
import $ from 'jquery';
import axios from 'axios';

export default {
    name:'Section3Component',
    props:[],
    data(){
      return{
        s3Title:'0원! 유료강의보다 좋은 무료강의들 👀',
        p : '무료 강의부터 가볍게 시작해 보세요.',
        slidelist : [],
      }
    },
    methods :{
    },
    created(){
      axios({
        url : './data/sec3text.json',
        methods : 'GET'
      })
      .then((res)=>{
        this.slidelist = res.data.slidelist;
      })
      .catch((err)=>{
        console.log(err)
      })
    },
    mounted() {

        //s3 아이콘 슬라이드 버튼
        $('.s3right-btn').on({
          click(){
            // 104.4px
            $('.s3-slid-wrap').stop().animate({marginLeft : -1200},300);
            $('.s3right-btn').animate({opacity: '0.2'})
            $('.s3left-btn').animate({opacity: '1'})
          }
        });
        $('.s3left-btn').on({
          click(){
            // 104.4px
            $('.s3-slid-wrap').stop().animate({marginLeft : 0},300);
            $('.s3left-btn').animate({opacity: '0.2'})
            $('.s3right-btn').animate({opacity: '1'})
          }
        });      

    },
}
</script>

<style>
  #wrap #main .section3 {
    width: 100%;
    height: 450px;
    display: flex;
    flex-flow: column;
    justify-content: center;
    align-items: center;
  }

  #wrap #main .section3 .top {
    width: 1200px;
    height: 64px;
    margin: 0 auto;
    padding-left: 10px;
  }

  #wrap #main .section3 .top .title {
    width: 100%;
  }

  #wrap #main .section3 .top .title h1 {
    width: 390px;
    font-size: 21px;
