    padding-bottom: 5px;
    font-weight: 600;
    cursor: pointer;
  }

  #wrap #main .section3 .top .title h1 span {
    width: 100%;
    width: 100%;
  }

  #wrap #main .section3 .top .title h1 span li {
    display: inline-block;
    content: '';
    width: 11px;
    height: 11px;
    border-right: 1px solid #1e1e1e;
    border-top: 1px solid #1e1e1e;
    transform: rotate(45deg);
    margin-top: -10px;
  }

  #wrap #main .section3 .top .title p {
    font-size: 14px;
