    justify-content: center;
    align-items: center;

  }

  /* 슬라이드 */
  #wrap #main .section3 .bottom .s3-container .s3-slide-box {
    width: 1200px;
    margin: 0 auto;
    height: 100%;
    position: relative;
    overflow: hidden;

  }

  /* 이미지 10개 */
  #wrap #main .section3 .bottom .s3-container .s3-slide-box>ul {
    width: 2400px;
    height: 324px;
    display: flex;
    align-items: center;
    justify-content: center;
    /* margin-left: -1200px; */
    position: relative;
    z-index: 3;
  }



  #wrap #main .section3 .bottom .s3-container .s3-slide-box>ul>li {
    width: 240px;
    height: 320px;
    padding-left: 10px;
    position: relative;
  }

  /* #wrap #main .section3 .bottom .s3-container .s3-slide-box>ul>li>a {
    width: 220px;
    height: 330px;
    display: flex;
    flex-flow: column;

  } */


  #wrap #main .section3 .bottom .s3-container .s3-slide-box>ul>li>a.s3-slide-content{
    width: 220px;
    height: 330px;
    display: flex;
    flex-flow: column;
    position: relative;
    z-index: 1;
  }
  #wrap #main .section3 .bottom .s3-container .s3-slide-box>ul>li:hover>a.s3-slide-content-hover {
      display:block;
  }


  #wrap #main .section3 .bottom .s3-container .s3-slide-box>ul>li>a>img {
    width: 100%;
    height: 150px;
    padding: 10px 0;
  }

  #wrap #main .section3 .bottom .s3-container .s3-slide-box>ul>li>a .course-title {
    width: 100%;
    font-size: 14px;
    font-weight: 600;
    color: #303030;
    line-height: 23px;
    padding-bottom: 8px;
  }

  #wrap #main .section3 .bottom .s3-container .s3-slide-box>ul>li>a .instructor {
    font-size: 13px;
    color: #7d7d7d;
  }

  #wrap #main .section3 .bottom .s3-container .s3-slide-box>ul>li>a .rating {
    font-size: 17px;
