    color: #303030;
    padding-bottom: 8px;
    color: #175cbe;
  }

  #wrap #main .section3 .bottom .s3-container .s3-slide-box>ul>li>a .tag {
    width: 100%;
  }

  #wrap #main .section3 .bottom .s3-container .s3-slide-box>ul>li>a .tag span {
    border: 1px solid transparent;
