    background-color: #e4fafb;
    padding: 2px 5px;
    color: #454545;
  }


  /* 이미지 호버 자식요소  a링크 이용 */

  #wrap #main .section3 .bottom .s3-container .s3-slide-box>ul>li>a.s3-slide-content-hover{
    position: absolute;
    z-index: 2;
    left: 0;
    top: 0;
    width: 225px;
    height: 305px;
    padding: 15px 8px;
    background-color: rgb(0,0,0,0.8);
    margin-top: 10px;
    margin-left: 8px;
    display: none;
  }



  #wrap #main .section3 .bottom .s3-container .s3-slide-box>ul>li>a.s3-slide-content-hover .course-title{
    font-size: 13px;
    line-height: 17px;
    font-weight: 600;
    color: #fff;
  }
  #wrap #main .section3 .bottom .s3-container .s3-slide-box>ul>li>a.s3-slide-content-hover .course-text {
    display: flex;
    flex-flow: column;
    padding: 10px 0;
  }
  #wrap #main .section3 .bottom .s3-container .s3-slide-box>ul>li>a.s3-slide-content-hover .course-text span{
    font-size: 13px;
    color: #59c3d6;
    line-height: 17px;
    padding: 0;
  }






  /* s3슬라이드버튼 */


  #wrap #main .section3 .bottom .s3-container .s3-btn-arrow {}


  /* 오른쪽 버튼*/
  #wrap #main .section3 .bottom .s3-container .s3-btn-arrow .right-btn-container {

  }


  #wrap #main .section3 .bottom .s3-container .s3-btn-arrow .right-btn-container .right-btn-box {
