    align-items: center;
    cursor: pointer;
  }

  #wrap #main .section3 .bottom .s3-container .s3-btn-arrow .right-btn-container .right-btn-box .s3right-btn {
    /* 00c471 */
    width: 44px;
    height: 44px;
    border: 1px solid #ccc;
    border-radius: 50%;
    background-color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 0px 0px 5px #a3a3a3
  }

  #wrap #main .section3 .bottom .s3-container .s3-btn-arrow .right-btn-container .right-btn-box .s3right-btn li {
    display: inline-block;
    width: 13px;
    height: 13px;
    /* background-color: #000000; */
    border-right: 2px solid #4e4e4e;
    border-top: 2px solid #4e4e4e;
    transform: rotate(45deg);
    margin-left: -5%;
  }


  /* 왼쪽버튼  */

  #wrap #main .section3 .bottom .s3-container .s3-btn-arrow .left-btn-container .left-btn-box {
    width: 104.4px;
    height: 93px;
    position: absolute;
    z-index: 6;
    top: 27%;
    left: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
  }

  #wrap #main .section3 .bottom .s3-container .s3-btn-arrow .left-btn-container .left-btn-box .s3left-btn {
    /* 00c471 */
    width: 44px;
    height: 44px;
    border: 1px solid #ccc;
    border-radius: 50%;
    background-color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 0px 0px 5px #a3a3a3
  }

  #wrap #main .section3 .bottom .s3-container .s3-btn-arrow .left-btn-container .left-btn-box .s3left-btn li {
    display: inline-block;
    width: 13px;
    height: 13px;
    /* background-color: #000000; */
    border-right: 2px solid #4e4e4e;
    border-top: 2px solid #4e4e4e;
    transform: rotate(225deg);
    margin-right: -5%;
  }


</style>