const section1 = {
    state:{
        isOn : false
    },
    getters:{
        getBtnIsOn(state){
            return state.isOn
        }
    },
    mutations:{
        setOpenSub(state, payload){
            state.isOn = payload.val
        },
        setCloseSub(state, payload){
            state.isOn = payload.val
        }

    },
    actions:{
        acOpenSub({commit},value){
            commit('setOpenSub',{val : value})
        },
        acCloseSub({commit},value){
            commit('setCloseSub',{val : value})
        }


    }
}
export default section1;