<script>
import axios from 'axios';

export default {
    name:'Section6Component',
    props:[],
    data(){
      return{
        s6contentbox :[]
      }
    },
    created(){
      axios({
        url : './data/sec6text.json',
        method: 'GET'
      }).then((res)=>{
        this.s6contentbox = res.data.s6contentbox
      }).catch((err)=>{
        console.log(err)
      })
    }

}
</script>

