<template>
    <section class="section section4">
        <div class="top">
          <div class="title">
            <h1>{{h4Title}}
              <span class="right-angle-box">
                <li class="right-angle"></li>
              </span>
            </h1>
            <p>{{pText}}</p>
          </div>
        </div>
        <div class="bottom">
          <div class="s4-container">
            <div class="s4-slide-box">
              <!-- 이미지 15개 5*3-->
              <ul class="s4-slid-wrap">
                <!-- 슬라이드 맨앞장 5장 = 1개 -->
                <li :class="['s4-slide', 's4slide'+(idx+1)]" v-for="item,idx in s4slide" :key="idx">
                  <a class="s4-slide-content" href="#">
                    <img :src="item.img" alt="">
                    <div class="course-title">{{item.title}}</div>
                    <div class="instructor">{{item.instructor}}</div>
                    <div class="rating">{{item.rating}}</div>
                    <div class="price">{{item.price}}</div>
                    <div class="tag">
                      <span>{{item.tag}}</span>
                    </div>
                  </a>
                </li>               
              </ul>
            </div>
            <!-- 버튼화살표 -->
            <div class="s4-btn-arrow">
              <!-- 누르면 displaynone -->
              <div class="right-btn-container">
                <div class="right-btn-box">
                  <!-- 누르면 슬라이드 이동 -->
                  <div class="s4right-btn">
                    <li></li>
                  </div>
                </div>
              </div>
              <!-- 누르면 displaynone -->
              <div class="left-btn-container">
                <div class="left-btn-box">
                  <!-- 누르면 슬라이드 이동 -->
                  <div class="s4left-btn">
                    <li></li>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
    </section>
</template>

<script>
import $ from 'jquery';
import axios from 'axios'
export default {
    name:'Section4Component',
    props:[],
    data(){
      return{
        h4Title:'왕초보도 할 수 있어요 💪',
        pText:'이미 검증된 쉽고 친절한 입문 강의!!',
        s4slide:[]
      }
    },
    created(){
      axios({
        url : './data/sec4text.json',
        method : 'GET'
      }).then((res)=>{
        this.s4slide = res.data.s4slide
      }).catch((err)=>{
        console.log(err)
      })
    },
    mounted() {

      let s4cnt = 0;

      function s4slide(){
        $('.s4-slid-wrap').stop().animate({left: `${-1200 * s4cnt}px` },600)

        if (s4cnt>1){
          $('.s4right-btn').animate({opacity: '0.2'});
        }
        if (s4cnt < 1){
          $('.s4left-btn').animate({opacity: '0.2'});
        }
      }

      $('.s4right-btn').on({
        click(){
          if (s4cnt>1){
            s4cnt = 1;
          }
          s4nextCount();
        }
      })
      $('.s4left-btn').on({
        click(){
          console.log(s4cnt)
          if(s4cnt>1){
            s4cnt = 2;
          }
          if (s4cnt < 1){
            s4cnt = 1;
          }
          s4prevCount();
          $('.s4right-btn').animate({opacity: '1'});
        }
      })



      //다음 카운트
      function s4nextCount() {
        s4cnt++;
        s4slide();
      }

      //이전 카운트
      function s4prevCount() {
        s4cnt --;    
        s4slide();    
      }



    },
}
</script>

<style scoped>

