<template>
      <section class="section section5">
        <div class="top">
          <div class="title">
            <h1>
              {{s6Title}}
              <span class="right-angle-box">
                <li class="right-angle"></li>
              </span>
            </h1>
            <p>아무것도 몰라도 따라오다 보면 전문가가 될 수 있어요!</p>
          </div>
        </div>
        <div class="bottom">
          <div class="s5-container">
            <div class="s5-slide-box">
              <!-- 이미지 10개 -->
              <ul class="s5-slid-wrap">
                <li :class="['s5-slide', 's5slide'+(idx+1)]" v-for="item,idx in s5Slide" :key="idx">
                  <a class="s5-slide-content" href="#">
                    <img :src="item.img" alt="">
                    <div class="course-title">{{item.title}}</div>
                  </a>
                </li>
              </ul>
            </div>
            <!-- 버튼화살표 -->
            <div class="s5-btn-arrow">
              <!-- 누르면 displaynone -->
              <div class="right-btn-container">
                <div class="right-btn-box">
                  <!-- 누르면 슬라이드 이동 -->
                  <div class="s5right-btn">
                    <li></li>
                  </div>
                </div>
              </div>
              <!-- 누르면 displaynone -->
              <div class="left-btn-container">
                <div class="left-btn-box">
                  <!-- 누르면 슬라이드 이동 -->
                  <div class="s5left-btn">
                    <li></li>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
</template>

<script>
import $ from 'jquery';
import axios from 'axios';

export default {
    name:'Section5Component',
    props:[],
    data(){
      return{
        s6Title:'기본부터 실무까지 제시해주는 로드맵 🏃🏻‍♀️',
        pText:'아무것도 몰라도 따라오다 보면 전문가가 될 수 있어요!',
        s5Slide :[]
      }
    },
    created(){

      axios({
        url : './data/sec5text.json',
        method : 'GET'
      }).then((res)=>{
        this.s5Slide = res.data.s5Slide
      }).axios(()=>{})
    },
    mounted(){ 


      let s5cnt = 0;

      function s5slide(){
        $('.s5-slid-wrap').stop().animate({marginLeft: `${-1200 * s5cnt}px` },600)

        if (s5cnt>1){
          $('.s5right-btn').animate({opacity: '0.2'});
        }
        if (s5cnt < 1){
          $('.s5left-btn').animate({opacity: '0.2'});
        }
      }

      $('.s5right-btn').on({
        click(){
          if (s5cnt>1){
            s5cnt = 1;
          }
          s5nextCount();
        }
      })
      $('.s5left-btn').on({
        click(){
          console.log(s5cnt)
          if(s5cnt>1){
            s5cnt = 2;
          }
          if (s5cnt < 1){
            s5cnt = 1;
          }
          s5prevCount();
          $('.s5right-btn').animate({opacity: '1'});
        }
      })



      //다음 카운트
      function s5nextCount() {
        s5cnt++;
        s5slide();
      }

      //이전 카운트
      function s5prevCount() {
        s5cnt --;    
        s5slide();    
      }


      }, //섹션5끝
}
</script>

