<template>
      <!-- 섹션9 기업아이콘 이미지 박스 -->
      <section class="section section9">
        <div class="s9container">
          <div class="title">
            <h1>이미 많은 기업 구성원들이 인프런에서 성장하고 있어요.</h1>
          </div>
          <div class="top">
            <div :class="['img'+idx+1]" v-for="item,idx in topImg" :key="idx"><img :src="item" alt=""></div>
          </div>
          <div class="bottom">
            <div :class="['img'+idx+10]" v-for="item,idx in bottomImg" :key="idx"><img :src="item" alt=""></div>
          </div>
        </div>
      </section>
</template>

<script>
import axios from 'axios'


export default {
    name:'Section9Component',
    props:[],
    data(){
      return{
        topImg:[],
        bottomImg:[],
      }
    },
    created(){
      axios({
        url : './data/sec9text.json'
      }).then((res)=>{
        this.topImg = res.data.topImg,
        this.bottomImg = res.data.bottomImg
      }).catch((err)=>{
        console.log(err)
      })
    },
}
</script>

