const modal = {
    state:{
        isOn : false
    },
    getters:{
        getIsOn(state){
            return state.isOn
        }
    },
    mutations:{
        setModalOpen(state,payload){
            state.isOn = payload.val
        },
        setModalClose(state,payload){
            state.isOn = payload.val
        }
    },
    actions:{
        acModalOpen({commit},value){
            commit('setModalOpen',{val : value})
        },
        acModalClose({commit},value){
            commit('setModalClose',{val : value})
        }
    }
}
export default modal;